import React from 'react'
import PropTypes from 'prop-types'
import Todo from '../components/Todo'
import NewTaskController from '../components/NewTaskController'
import '../style.css'

class Todos extends React.Component {
  constructor(props) {
    super(props);
    this.state = {todos: []};
  }
  componentDidMount(){
    const items = [
      {text: "Take out trash"},
      {text: "Ask out that girl"},
      {text: "Sleep"}
    ];
    this.setState({
      todos: items
    }); // update state w/ array
  }


  render(){
    const currentTodos = this.state.todos; // returns all todos
    console.log(currentTodos);

    return (
        <div>
        <div className='card'>
          <div className='card-body'>
            {this.state.todos.map( (item, index) => <Todo text={item.text} /> ) }
          </div>
        </div>
        <NewTaskController />
        </div>
          );
  }
}

export default Todos;
